import { NextRequest, NextResponse } from "next/server"

import { users } from "@/app/api/_data"
import { db } from "@/lib/db"

export const dynamic = "force-dynamic"

// POST /api/auth/login
// Tries real DB users first (SQLite via Prisma), then falls back to in-memory users for demo accounts.
// Returns minimal profile info used by the frontend for routing.
export async function POST(req: NextRequest) {
  const body = (await req.json().catch(() => null)) as { email?: string; password?: string } | null

  if (!body?.email || !body?.password) {
    return NextResponse.json({ error: "Email and password are required" }, { status: 400 })
  }

  const email = body.email.toLowerCase()

  // 1) Try real DB user (SQLite)
  try {
    const dbUser = await db.user.findFirst({
      where: { email },
      include: { business: true },
    })

    if (dbUser && dbUser.password === body.password) {
      // Map DB role/business type to app role used in the frontend
      let appRole: "admin" | "staff" | "charity" | "farmer" = "staff"

      if (dbUser.role === "ADMIN") {
        appRole = "admin"
      } else if (dbUser.role === "STAFF") {
        appRole = "staff"
      } else if (dbUser.role === "PARTNER") {
        const bt = dbUser.business?.type
        if (bt === "NGO") appRole = "charity"
        else if (bt === "FARM") appRole = "farmer"
        else appRole = "staff"
      }

      return NextResponse.json({
        userId: dbUser.id,
        role: appRole,
        organizationName: dbUser.business?.name || "My Business",
        cafeId: dbUser.businessId, // use businessId as cafeId equivalent
      })
    }
  } catch (error) {
    console.error("[POST /api/auth/login] DB lookup failed, falling back to mock users:", error)
  }

  // 2) Fallback: in-memory users (existing demo behavior)
  const user = users.find(
    (u) => u.email.toLowerCase() === email && u.passwordHash === body.password,
  )

  if (!user) {
    return NextResponse.json({ error: "Invalid credentials" }, { status: 401 })
  }

  return NextResponse.json({
    userId: user.id,
    role: user.role,
    organizationName: user.organizationName,
    cafeId: user.cafeId,
  })
}

